export default {
	props: {
		iconClass: String,
		mustFill: Boolean,
		placeHolder: String,
		itemName: String,
		inputType: String
	},
	data() {
		return {
			isMustFill: this.mustFill,
			isTipShow: false,
			tips: '',
			inputCentent: ''
		}
	},
	methods: {
		judementInputContent() {
			if(this.inputCentent.length !== 0) {
				this.isTipShow = false
				this.$emit('getInputCentent', {type: this.itemName, content: this.inputCentent})
			}
			if(this.mustFill) {
				if(this.inputCentent.length === 0) {
					this.isTipShow = true
					this.tips = `${this.itemName}不可以为空`
					this.$emit('getInputCentent', {type: this.itemName, content: this.inputCentent})
				}
			}
		}
	}
}
